.jquery-accordion-menu {
  position: relative;
  box-shadow: 0 20px 50px #333;
}

.jquery-accordion-menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.jquery-accordion-menu ul li {
  width: 100%;
  display: block;
  position: relative;
}

.jquery-accordion-menu ul li a {
  width: 100%;
  padding: 14px 22px;
  float: left;
  text-decoration: none;
  color: #fff;
  border-bottom: 1px solid #f0f0f0;
  font-size: 15px;
  background-color: #302e4e;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  transition: color .2s linear, background .2s linear;
}

.jquery-accordion-menu > ul > li.active > a,
.jquery-accordion-menu > ul > li:hover > a {
  color: #fff;
  background-color: #44416e;
}

.jquery-accordion-menu > ul > li > a {
  border-bottom: solid 1px #3b424d;
}

.jquery-accordion-menu .submenu-indicator {
  float: right;
  right: 22px;
  position: absolute;
  line-height: 19px;
  font-size: 20px;
  transition: transform .3s linear;
}

.jquery-accordion-menu ul ul .submenu-indicator {
  line-height: 16px;
}

.jquery-accordion-menu .submenu-indicator-minus > .submenu-indicator {
  transform: rotate(45deg);
}

.jquery-accordion-menu ul ul,
.jquery-accordion-menu ul ul li ul {
  width: 100%;
  display: none;
  position: static;
}

.jquery-accordion-menu ul ul li {
  clear: both;
  width: 100%;
}

.jquery-accordion-menu ul ul li a {
  width: 100%;
  font-size: 12px;
  background-color: #4e4a7e;
  border-top: none;
  position: relative;
  border-left: solid 6px transparent;
  transition: border .2s linear;
}

.jquery-accordion-menu ul ul li:hover > a {
  border-left-color: #414956;
}

.jquery-accordion-menu ul ul > li > a {
  padding-left: 30px;
}

.jquery-accordion-menu ul ul > li > ul > li > a {
  padding-left: 45px;
}

.jquery-accordion-menu ul ul > li > ul > li > ul > li > a {
  padding-left: 60px;
}

